import React from 'react';

const GalleryItemList = ({
  item,
  toTrue,
  setIditem
}) => {

  console.log(item);

  return (
    <>
      <div className="picture-miniature" key={item.id}>
        <img className="picture-visuel" src={`https://loupargent-oclock.fr/img/${item.picture_file}`} alt="Miniature de l'item" onClick={() => {
          toTrue(); 
          setIditem(item.id)
        }}/>
      </div>
    </>
  )
}

export default GalleryItemList;