import React, { useEffect, useState } from 'react';
import Loading from '../../UIElement/Loading/Loading';

import Modal from '../../UIElement/Modal/Modal';
import GalleryItemList from './GalleryItemList';
import Pagination from '../../UIElement/Pagination/PaginationContainer';
import Illustration from './Illustration';

import './stylesGallery.scss';

const Pictures = ({ fetchGallery, gallery, errorMessage}) => {

  useEffect(() => {
    fetchGallery();
  }, [fetchGallery]);

  const [pictureModal, setPictureModal] = useState(false);

  const toTrue = () => setPictureModal(true);
  const toFalse = () => setPictureModal(false);

  const [idIllustration, setIdIllustration] = useState();

  const findGallery = gallery.find(illustration => illustration.id === idIllustration); 

  if(gallery.length === 0) {
    return <Loading />
  } else {
    return (
      <>
        <Modal
          show={pictureModal}
          onCancel={toFalse}
          button={
              <button onClick={toFalse} style={{ backgroundColor: 'red' }}>
              fermer
              </button>
          }
          content={<Illustration illustration={findGallery}/>}
        />

        <h2>Galerie</h2>
        
        <div className="pictures-listing">
          <Pagination
            data={gallery}
            Component={GalleryItemList}
            toTrue={toTrue}
            setIdIllustration={setIdIllustration}
          />
        </div>
      </>
    )
  }
}

export default Pictures;
